.common-container {
    margin-top: 40px;
    display: flex;
    justify-content: center;
}

.todos-container {
    width: 40%;
}

.todo-container {
    margin: 10px;
}

.category-container {
    color: blue;
    font-style: italic;
}

.delete-btn {
    background-color: white;
    border: 1px solid blue;
    border-radius: 15%;
    font-size: 20px;
    letter-spacing: 0.1em;
    font-weight: bold;
}

.delete-btn:focus {
    outline: none;
}

.filter-container {
    width: 20%;
    padding: 10px;
    border: 1px solid blue;
    border-radius: 15%;
    text-align: left;
}

.category-button {
    display: block;
    border: none;
    font-size: 18px;
}